//import scrollTo from 'react-scroll-to-component'

class Nav extends React.Component {

	constructor(props) {
		super(props)

		this.handleClick = this.handleClick;
	}

	componentDidMount() {
		this.scrollTo = require('react-scroll-to-component')
	}

	handleClick (item) {
		this.scrollTo()
	}

	render () {
		return (
			<nav className="main-nav">
					{this.props.links.map((link, i) => {
						return (
							<a className="main-nav__link"
							key={i}
							href={link.href}
							// onClick={(event) => {
							// 	event.preventDefault()
							// 	this.handleClick(link.href)
							// }}
							>
								{link.name}
							</a>
						)
					})}
			</nav>
		)
	}

}

export default Nav
